import { ActionListContext, IActionData } from "@droid/project-interface"
import { ActionUtilsMap } from "@droid/project-interface"
import { Box, Button } from "node_modules/@mui/material"
import {ButtonConfirm, getParent, toProxy} from "@fanfanlo"
import { useContext } from "react"
export function Action({actionData}:{actionData:IActionData<any,any>}){
    const list = useContext(ActionListContext)
    const utils = ActionUtilsMap[actionData.type]
    function onDeleteComfirmed(){
        const index = list.indexOf(actionData)
        if(index === -1)return
        list.splice(index, 1)
    }
    return <Box sx={{
        position: 'relative',
        padding: '2px',
        marginTop:"2px",
        marginBottom:"2px",
        '&::after': {
            content: '""',
            position: 'absolute',
            right: 0,
            top: '5px',
            bottom: '5px',
            borderRight: '1px solid red',
        },
        '&::before': {
            content: '""',
            position: 'absolute',
            left: 0,
            top: '5px',
            bottom: '5px',
            borderLeft: '1px solid red',
        }
    }}>
        {utils.UI(actionData)}
        <Box sx={{textAlign:"center"}}>
            
        <ButtonConfirm title="" content="" onConfirm={onDeleteComfirmed}>
                <Button 
                id="del-action-btn"
                data-id="del-action-btn"
                ></Button>
            </ButtonConfirm>
        </Box>
    </Box>
}
