import { IActionOrTriggerListPageParams } from "@app/static";
import { Button } from "@mui/material";
import { useRouter } from "next/router";
import { PropsWithChildren } from "react";
export interface IActionListOrTriggerListPageLinkProps extends PropsWithChildren{
  query:IActionOrTriggerListPageParams
  attrs:object
  path:'/action-list'|'/trigger-list'
  btnId:string
}
export function ActionListOrTriggerListPageLink({query, attrs, path, btnId, children}:IActionListOrTriggerListPageLinkProps){
  const router = useRouter();
  query.backPath = router.pathname
  function handleClick(){
    const list = Object.keys(query).map(key=> [key, query[key]!!.toString()])
    const q = new URLSearchParams(list);
    const href = `${path}?${q.toString()}`
    router.push(href)
  }
  
  return (
    <Button onClick={handleClick} 
    {...attrs}
    // id="link-router-to-action-list"
    // data-id="link-router-to-action-list"
    id={btnId}
    data-id={btnId}
    >{children}</Button>
  )
}