import { ActionOrTriggerListTypeEnum, IActionOrTriggerListPageParams } from "@app/static";
import { ActionGroupContext, ActionListContext, getActionGroupElement, IActionGroup, TriggerActionContext, TriggerActionGroupContext } from "@droid/project-interface";
import { toProxy, useProxyWatch } from "@fanfanlo";
import { Box, FormControlLabel, FormGroup, Grid2, Switch } from "@mui/material";
import { useContext } from "react";
import { GroupCheck } from "../../../components/group/GroupCheck";
import { ActionListOrTriggerListPageLink } from "../../../components/list-page-link/ActionListOrTriggerListPageLink";
import { ProjectEditInfoContext } from "../../../contexts";
import { ActionList } from "./action-list/ActionList";
import { useTranslation } from "@fanfanlo";
export function ActionGroup({ actionGroup }: { actionGroup: IActionGroup }) {
    const {t} = useTranslation("droid-project/project/ui/project/edit-project/trigger-action-group/trigger-action/action-group/content")
    const editInfo = useContext(ProjectEditInfoContext)
    const triggerActionGroup = useContext(TriggerActionGroupContext)
    const triggerAction = useContext(TriggerActionContext)

    const query: IActionOrTriggerListPageParams = {
        projectId: editInfo.project.id,
        triggerActionGroupId: triggerActionGroup.id,
        triggerActionId: triggerAction.id,
        actionGroupId: actionGroup.id,
        type: ActionOrTriggerListTypeEnum.addAction,
        actionType: '',
    }
    const [isSequential] = useProxyWatch(actionGroup, 'isSequential', actionGroup.isSequential)
    const [listSize] = useProxyWatch(actionGroup.list, 'length', actionGroup.list.length)
    function setIsSequential(value: boolean) {
        toProxy(actionGroup).isSequential = value
    }
    return (
        <ActionGroupContext.Provider value={actionGroup}>
            <ActionListContext.Provider value={actionGroup.list}>

                <Box sx={{ border: '2px solid #ccc', padding: '4px' }}>
                    <Grid2 container sx={{ alignItems: 'center' }}>
                        <Grid2 size={"grow"}>
                            <Box>{t("content.title")}</Box>
                        </Grid2>
                        <Grid2>
                            {listSize > 1 && <FormGroup>
                                <FormControlLabel control={<Switch checked={isSequential} 
                                data-id="action-group-checkbox-is-sequential.122f"
                                aria-checked={isSequential}
                                data-checked={isSequential}
                                onChange={(e) => { setIsSequential(e.target.checked) }} />} label={t("content.sequential")} />
                            </FormGroup>}
                        </Grid2>
                        <Grid2>
                            <Box>
                                <ActionListOrTriggerListPageLink query={query} attrs={getActionGroupElement(actionGroup)} path="/action-list" btnId="link-router-to-action-list" children={t("content.list")} />
                            </Box>
                        </Grid2>
                    </Grid2>
                    <GroupCheck group={actionGroup} />
                    <ActionList />
                </Box>
            </ActionListContext.Provider>
        </ActionGroupContext.Provider>
    )
}

