import { IProjectData } from "@droid/project-interface";
import { getProxyTarget, proxyUtils } from "@fanfanlo";
import _ from "lodash";
import { IProjectHistory } from "./IProjectHistory.intf";

export const projectsHistoryMap = new Map<string, IProjectHistory[]>()

export function addProjectHistory(history:IProjectHistory){
    proxyUtils.runPauseProxyFn(()=>{
        history.project = _.cloneDeep(getProxyTarget(history.project))
    })
    const p = history //_.cloneDeep(getProxyTarget(history))
    const list = projectsHistoryMap.get(history.project.id)
    if(!list){
        projectsHistoryMap.set(history.project.id, [p])
        return
    }
    list.push(p)
}

export function getProjectHistory(projectId:string){
    return projectsHistoryMap.get(projectId) || []
}

export function removeProjectHistory(projectId:string){
    projectsHistoryMap.delete(projectId)
}

export function getProjectLastHistory(projectId:string){
    const list = getProjectHistory(projectId)
    return list[list.length - 1]
}

export function takeProjectSnapshoot(project:IProjectData):boolean{
    const last = getProjectLastHistory(project.id)
    if(last && _.isEqual(last.project, project)){
        return false
    }
    addProjectHistory({
        index: last ? last.index + 1 : 0,
        project,
        date: new Date().toISOString()
    })   
    return true
}

export function projectHistoryIsChanged(project:IProjectData):boolean{
    const last = getProjectLastHistory(project.id)
    return !last || !_.isEqual(last.project, project)

}

export function isSameProjectHistoryId(id:number, projectId:string):boolean{
    const last = getProjectLastHistory(projectId)
    return last?.index === id
}