import { IProjectData, projectStore } from '@droid/project-interface';
import { Dispatcher, loadI18nValue, Log, toProxy } from '@fanfanlo';
import { IProjectEditInfo } from './IProjectEditInfo.intf';
// import { projectStore } from '../../storage';    
import toast from 'react-hot-toast';
const map: { [key: string]: IProjectEditInfo } = toProxy({});
const dispatcher = new Dispatcher();
const projectSavedEvent = 'project-saved';
export const editProjectInfoUtils = {
  edit: (project: IProjectData) => {
    // const info = map[project.id] || (map[project.id] = createProjectEditInfo(project));
    const info = (map[project.id] = createProjectEditInfo(project));
    return info;
  },
  getEditInfoById: (id: string) => {
    if (map[id]) return map[id];
    const project = projectStore.findProject(id);
    if (project) return (map[id] = createProjectEditInfo(project));
    return null;
  },
  listenProjectSaved: (fn: (project: IProjectData) => void) => {
    return dispatcher.addListener(projectSavedEvent, fn);
  },
};

export function createProjectEditInfo(project: IProjectData): IProjectEditInfo {
  const info = toProxy({
    project,
    askStay: false,
    giveup: false,
    updated: false,
    historyId: 0,
    testTime: new Date().toLocaleString(),
    save: async () => {
      info.updated = false;
      info.askStay = false;
      info.giveup = false;
      projectStore.saveProject(project);
      dispatcher.dispatch(projectSavedEvent, project);
      const s = await loadI18nValue('droid-project/project/manager/content', 'edit-project-info-utils.projectSaved');
      toast.success(s);
    },
  });
  return info;
}
