import { ITriggerData, ITriggerJavaData, ITriggerShellData, TriggerListContext, TriggerGroupContext } from "@droid/project-interface"
import { Box } from "@mui/material"
import { getUpdateVersion, useWatchUpdates } from "@fanfanlo"
import { Trigger } from "./trigger/Trigger"
import { useContext, useEffect } from "react"
export function TriggerList(){
    const triggerGroup = useContext(TriggerGroupContext)
    const [l] = useWatchUpdates<ITriggerData<ITriggerShellData, ITriggerJavaData>[]>(triggerGroup.list)
    // useEffect(()=>{
    //     console.log('llllllllllllllllllllllll', l)
    //     console.log('triggerGroupppppppppp', triggerGroup)
    // }, [l])
    return (
        <TriggerListContext.Provider value={triggerGroup.list}>
            <Box>
                {triggerGroup.list.map((trigger) => (
                    <Trigger key={getUpdateVersion(trigger).version} triggerData={trigger} />
                ))}
            </Box>
        </TriggerListContext.Provider>
    )
}

