import { ActionOrTriggerListTypeEnum, IActionOrTriggerListPageParams } from "@app/static";
import { ITriggerGroup, TriggerActionContext, TriggerActionGroupContext, TriggerGroupContext, childToProjectDataUtils } from "@droid/project-interface";
import { useTranslation } from "@fanfanlo";
import { Box, FormControlLabel, FormGroup, Grid2, Switch } from "@mui/material";
import { useContext } from "react";
import { ActionListOrTriggerListPageLink } from "../../../components/list-page-link/ActionListOrTriggerListPageLink";
import { ProjectEditInfoContext } from "../../../contexts";
import { TriggerList } from "./trigger-list/TriggerList";
import { toProxy, useProxyWatch } from "@fanfanlo";
import { GroupCheck } from "../../../components/group/GroupCheck";
export function TriggerGroup({ triggerGroup }: { triggerGroup: ITriggerGroup }) {
    const {t} = useTranslation("droid-project/project/ui/project/edit-project/trigger-action-group/trigger-action/trigger-group/content")
    
    const editInfo = useContext(ProjectEditInfoContext)
    const triggerActionGroup = useContext(TriggerActionGroupContext)
    const triggerAction = useContext(TriggerActionContext)
    
    const query: IActionOrTriggerListPageParams = {
        projectId: editInfo.project.id,
        triggerActionGroupId: triggerActionGroup.id,
        triggerActionId: triggerAction.id,
        triggerGroupId: triggerGroup.id,
        type: ActionOrTriggerListTypeEnum.addAction,
        actionType: '',
    }

    const [isSequential] = useProxyWatch(triggerGroup, 'isSequential', triggerGroup.isSequential)
    const [listSize] = useProxyWatch(triggerGroup.list, 'length', triggerGroup.list.length)
    function setIsSequential(value: boolean) {
        toProxy(triggerGroup).isSequential = value
    }
    return (
        <TriggerGroupContext.Provider value={triggerGroup}>
            <Box sx={{ border: '2px solid #a4a737', padding: '4px', marginBottom: "2px", marginTop: "2px" }}>
                <Grid2 container sx={{ alignItems: 'center' }}>
                    <Grid2 size={"grow"}>
                        <Box>{t("content.title")}</Box>
                    </Grid2>
                        <Grid2>
                            {listSize > 1 && <FormGroup>
                                <FormControlLabel control={<Switch checked={isSequential} 
                                data-id="trigger-group-checkbox-is-sequential.122f"
                                aria-checked={isSequential}
                                data-checked={isSequential}
                                onChange={(e) => { setIsSequential(e.target.checked) }} />} label={t("content.sequential")} />
                            </FormGroup>}
                        </Grid2>
                    <Grid2>
                        <Box>
                            <ActionListOrTriggerListPageLink query={query} attrs={childToProjectDataUtils.triggerGroup.getTriggerGroupElement(triggerGroup)} path="/trigger-list" btnId="link-router-to-trigger-list" children={t("content.list")} />

                        </Box>
                    </Grid2>
                </Grid2>
                <GroupCheck group={triggerGroup} />
                <TriggerList/>
            </Box>
        </TriggerGroupContext.Provider>
    )
}


