import { childToProjectDataUtils, createDefultTriggerAction, TriggerActionGroupContext } from "@droid/project-interface"
import { EditableText, getUpdateVersion, toParent, useWatchListOperation, } from "@fanfanlo"
import AddIcon from '@mui/icons-material/Add'
import HandymanIcon from '@mui/icons-material/Handyman'
import NotificationsNoneIcon from '@mui/icons-material/NotificationsNone'
import TableRowsIcon from '@mui/icons-material/TableRows'
import { Box, Grid2, IconButton } from "@mui/material"
import { useContext, useState } from "react"
import { useTranslation } from "react-i18next"
import { ProjectEditInfoContext } from "../contexts"
import { TriggerAction } from "./trigger-action/TriggerAction"
export function TriggerActionGroup() {
    const { t } = useTranslation('droid-project/project/ui/project/edit-project/trigger-action-group/content')
    const editInfo = useContext(ProjectEditInfoContext)
    const [groupName, setGroupName] = useState(editInfo.project.triggerActionGroup.name || t('TriggerActionGroup.unamed'))

    const [list] = useWatchListOperation(editInfo.project.triggerActionGroup.list)
    function handleGroupNameChange(name: string) {
        editInfo.project.triggerActionGroup.name = name
        setGroupName(name || t('TriggerActionGroup.unamed'))
    }
    const triggerActionGroup = editInfo.project.triggerActionGroup
    function addTriggerAction() {
        const newTriggerAction = createDefultTriggerAction()
        toParent(triggerActionGroup).list.push(newTriggerAction)
    }
    return (
        <TriggerActionGroupContext.Provider value={triggerActionGroup}>
            <Box sx={{ border: '2px solid rgb(217, 217, 217)', padding: '4px' }}
            {...childToProjectDataUtils.triggerActionGroup.getTriggerActionGroupElementByProp(triggerActionGroup.list)}
            data-id-trigger-action-group={triggerActionGroup.id}>
                <Grid2 container sx={{ alignItems: 'center' }}>
                    <Grid2 container sx={{ alignItems: 'center' }}>
                        <NotificationsNoneIcon />
                        <HandymanIcon />
                        <TableRowsIcon />
                    </Grid2>
                    <Grid2 size={"grow"} sx={{ alignItems: 'center', width: "100%" }}>

                        <Box sx={{ width: "100%" }}>
                            <EditableText setText={handleGroupNameChange} >
                                <span
                                data-id="rename-groupname-btn-a322" style={{ backgroundColor: 'red' }}>{groupName}</span>
                            </EditableText>
                        </Box>
                    </Grid2>
                    <Grid2>
                        <Box>
                            <IconButton
                            data-id="add-trigger-action-btn.22fab779-75e2-4e34-82f4-64667e7fc017"
                             onClick={addTriggerAction}>
                                <AddIcon />
                            </IconButton>
                        </Box>
                    </Grid2>
                </Grid2>
                <Box>
                    {list.map((ta, index) => {
                        const triggerAction = triggerActionGroup.list[index]
                        return <TriggerAction key={getUpdateVersion(triggerAction).version}
                        triggerAction={triggerAction} />
                    })}
                </Box>
            </Box>
        </TriggerActionGroupContext.Provider>
    )
}


