import { EditProjectBuilder } from "@droid/project"
import { AppbarContainer, AppShell, IAppbarProps } from "@app/ui"
import { initActionListGroups } from "@src/components/page/action-list/action.data"
import { initTriggerListGroups } from "@src/components/page/trigger-list/trigger.data"



export default function ProjectEditor(){
    const {Content, Title, dispatcher} = EditProjectBuilder()
    initActionListGroups()
    initTriggerListGroups(  )
    const appBarProps:IAppbarProps = {
        title:<Title/>,
        back:true,
        backDispatcher:dispatcher
    }
    return (
        <AppShell>
            <AppbarContainer appbarProps={appBarProps}
            titleConf={{ns:"homepage/pages/project-editor/content", key:"editor.title"}}>
                <Content />
            </AppbarContainer>
        </AppShell>
    )


}



