import { IJsBridgeEventMap, jsBridgeDispatcher } from "@droid/android"
import { mergeProjectDataToDefultData, ProjectContext, projectStore } from "@droid/project-interface"
import { Dispatcher, EditableText, getParentProxyTarget, IDispatchEvent, Log, objectCountUtils, toParent, toProxy, useProxyWatch, watchUpdates } from "@fanfanlo"
import CloseIcon from '@mui/icons-material/Close'
import { Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, IconButton, Paper, styled } from "@mui/material"
import { useRouter } from "next/router"

import { useContext, useEffect } from "react"
import { useTranslation } from "react-i18next"
import { takeProjectSnapshoot } from "../../../../history/projectsHistoryMap"
import { editProjectInfoUtils } from "../../../manager/edit-project-info-utils"
import { ProjectEditInfoContext } from "./contexts"
import { TriggerActionGroup } from "./trigger-action-group/TriggerActionGroup"

import SaveIcon from '@mui/icons-material/Save'
import _ from "lodash"
// import { IAppBarBackAskEvent } from "@app/static"

interface IAppBarBackAskEvent{
    defaultPrevented:boolean
}
const BootstrapDialog = styled(Dialog)(({ theme }) => ({
    '& .MuiDialogContent-root': {
        padding: theme.spacing(2),
    },
    '& .MuiDialogActions-root': {
        padding: theme.spacing(1),
    },
}));

function ProjectContent() {
    const project = useContext(ProjectContext)
    return <Box
    data-id-project={project.id}>
        <Paper>
            <TriggerActionGroup />
        </Paper>
    </Box>
}

function StayAlert() {
    const editInfo = useContext(ProjectEditInfoContext)
    const logger = Log.createCountedLogger(false, 'project-editor-effect')
    const [askStay] = useProxyWatch(editInfo, 'askStay', editInfo.askStay)
    const router = useRouter()

    function toLeave() {
        editInfo.askStay = false
        editInfo.giveup = true
        router.back()
        editInfo.giveup = false
        editInfo.updated = false
    }
    function handleClose() {    
        editInfo.askStay = false
    }
    function handleLeaveWithoutSave() {
        toLeave()
        projectStore.deleteDraft(editInfo.project.id)
    }
    function handleSaveAndLeave() {
        editInfo.save()
        toLeave()
        projectStore.deleteDraft(editInfo.project.id)
    }
    function handleStayAndEdit() {
        editInfo.askStay = false
        editInfo.giveup = false
        logger.log('handleStayAndEdit fn called ', 'objectCount=', objectCountUtils.getObjectCount(editInfo), 'editInfo=', editInfo)
    }

    function handleSaveToDraftAndLeave() {
        // projectStore.save
        projectStore.addDraft(editInfo.project)
        toLeave()
    }

    return <Box>
        <BootstrapDialog
            onClose={handleClose}
            aria-labelledby="customized-dialog-title"
            open={askStay}
        >
            <DialogTitle sx={{ m: 0, p: 2 }} id="customized-dialog-title">
                :
            </DialogTitle>
            <IconButton
                aria-label="close"
                onClick={handleClose}
                sx={(theme) => ({
                    position: 'absolute',
                    right: 8,
                    top: 8,
                    color: theme.palette.grey[500],
                })}
            >
                <CloseIcon />
            </IconButton>
            <DialogContent dividers>

            </DialogContent>
            <DialogActions>
                <Button onClick={handleLeaveWithoutSave}></Button>
                <Button onClick={handleSaveToDraftAndLeave}></Button>
                <Button onClick={handleSaveAndLeave}></Button>
                <Button onClick={handleStayAndEdit}></Button>
            </DialogActions>
        </BootstrapDialog>
    </Box>
}
function ProjectTools(){
    const {t} = useTranslation('droid-project/project/ui/project/edit-project/content')
    function onRunClick(){

    }
    return(
        <>
            <Box>
                <Button onClick={onRunClick}>{t("content.projectTools.run")}</Button>
            </Box>
        </>
    )
}
export function EditProjectBuilder() {
    const dispatcher = new Dispatcher()
    const router = useRouter()
    const { id } = router.query
    let project = projectStore.findDraftOrSavedProject((id || '-1') as string) || mergeProjectDataToDefultData({ id: (id || '-1') as string })
    const createProject = project
    const draftPorjct = project = projectStore.addDraft(createProject)
    const proxyProject = project = toProxy(draftPorjct)
    const parentProject = project = toParent(proxyProject)
    const editInfo = editProjectInfoUtils.edit(parentProject)
    type NeedStay = boolean
    function checkUpdated():NeedStay{
        const logger = new Log(false, 'checkUpdated_fn')
        logger.isPaused = true
        const c = projectStore.findSavedProject(project.id)
        logger.log('checkUpdated fn called',  'c=', c)
        editInfo.updated = !c || !_.isEqual(c, project)
        logger.log('editInfo.updated=', editInfo.updated)
        if(editInfo.updated && !editInfo.giveup){
            editInfo.askStay = true
            logger.log('checkUpdated fn called',  'editInfo.askStay=', editInfo.askStay)
            return true
        }
        logger.log('checkUpdated fn called2',  'editInfo.askStay=', editInfo.askStay)
        return false
    }
    function Content() {
        useEffect(() => {
            takeProjectSnapshoot(project)
            if (!project) return
            const unsub = watchUpdates(getParentProxyTarget(project), () => {
                takeProjectSnapshoot(project)
                editInfo.updated = true
                // console.log('project updated2', project)
            })
            return () => {
                unsub()
            }
        }, [])
        useEffect(() => {
            const unsub = jsBridgeDispatcher.addListener<IJsBridgeEventMap>("checkBrowserCanBack", (event: IDispatchEvent) => {
                const canBack = event.result = checkUpdated()
                console.log('ooooooooooooooooooooooooo checkBrowserCanBack_fn', event, 'canback=', canBack)
                if(canBack)projectStore.deleteDraft(project.id)
            })
            return () => {
                unsub()
            }
        }, [])

        useEffect(()=>{
            return dispatcher.addListener('back', (e:IAppBarBackAskEvent)=>{
                const canBack = e.defaultPrevented = checkUpdated()
                if(canBack)projectStore.deleteDraft(project.id)
            })
        },[])
        const p = toProxy(editInfo.project)
        return (
            <ProjectEditInfoContext.Provider value={editInfo}>
                <ProjectContext.Provider value={p}>
                    <Box sx={{width:"100%"}}>
                        <ProjectTools />
                        <ProjectContent />
                        <StayAlert />
                    </Box>
                </ProjectContext.Provider>
            </ProjectEditInfoContext.Provider>
        )
    }

    function Title() {
        const { t } = useTranslation('droid-project/project/ui/project/edit-project/content')
        const clickToSetProjectName = t('content.clickToSetProjectName')
        const [name] = useProxyWatch(project, 'name', clickToSetProjectName)
        function setName(name: string) {
            project.name = name
        }
        return (<div style={{ display: 'flex', flexDirection: 'row', gap: 16, width: "100%", alignItems: "center" }}>
            <span style={{ flexGrow: 1, paddingLeft: "2rem" }}>
                <EditableText setText={setName} >
                    <h2><span
                    id="project-name-editor"
                    data-id="project-name-editor" 
                    >
                        {name || clickToSetProjectName}
                    </span></h2>
                </EditableText>
            </span>
            <IconButton onClick={() => { editInfo.save(); }}>
                <SaveIcon />
            </IconButton>
        </div>
        )
    }
    return { Content, Title, dispatcher }
}