import { actionGroupType, childToProjectDataUtils, getActionGroupElement, GroupCheckList1, GroupCheckList1Context, GroupCheckList2, IActionGroup, IChildToProjectData, IGroup, ITriggerActionGroup, ITriggerGroup, triggerActionGroupType, triggerGroupType } from '@droid/project-interface';
import {
  ButtonConfirm,
  getParent,
  getUpdateVersionKey,
  Log,
  objectCountUtils,
  useProxyWatch,
  useWatchUpdates
} from '@fanfanlo';
import { Box, Button, Checkbox, FormControlLabel, FormGroup, Grid2, IconButton } from '@mui/material';
import { useContext, useEffect } from 'react';

import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';

function getProjectDataByGroup(group:IGroup<any>){
    let projectData:IChildToProjectData = {}
    switch(group.type){
        case actionGroupType:
            getActionGroupElement(group as IActionGroup,'', '', projectData)
            break;
        case triggerGroupType:
          childToProjectDataUtils.triggerGroup.getTriggerGroupElement(group as ITriggerGroup,'', '', projectData)
            break;
        case triggerActionGroupType:
          childToProjectDataUtils.triggerActionGroup.getTriggerActionGroupElement(group as ITriggerActionGroup,'', '', projectData)
            break;
    }
    return projectData
}
function Check2({ list, index }: { list: GroupCheckList2; index: number }) {
  const logger = new Log(false, 'Check2_fn');
  logger.isPaused = true
  logger.log('list=', list)
  const [l] = useWatchUpdates(list);
  const groupCheckList1Context = useContext(GroupCheckList1Context);
  function onRemoveClick() {
    groupCheckList1Context.splice(index, 1);
  }
  logger.log('l=', l)
  // const check1 = getParent<GroupCheckList1>(list)
  // console.log('check1=', check1)
  // const group = getParent<IGroup<any>>(check1!)!
  // console.log('group=', group)
  // const projectData = getProjectDataByGroup(group)
  // console.log('projectData=', projectData)
  // const globalId = `project`
  return (
    <>
      <Grid2 container>
        <Grid2 container size={'grow'}>
          {l.map((check, index) => (
            <Grid2 key={objectCountUtils.getNewCountId().toString()}>
              <FormGroup>
                <FormControlLabel
                  label={index.toString()}
                  control={
                    <Checkbox
                    data-id="group-check-checkbox"
                      data-checked={check}
                      data-value={check}
                      checked={check}
                      onChange={e => {
                        list[index] = e.target.checked;
                      }}
                    />
                  }
                />
              </FormGroup>
            </Grid2>
          ))}
        </Grid2>
        <Grid2>
          <ButtonConfirm title="" onConfirm={onRemoveClick} content="">
            <IconButton data-id="group-check-remove" >
              <DeleteOutlineIcon />
            </IconButton>
          </ButtonConfirm>
        </Grid2>
      </Grid2>
    </>
  );
}

function Check1({ list }: { list: GroupCheckList1 }) {
  const logger = new Log(false, 'Check1_fn');
  logger.isPaused = true
  logger.log('Check1 list=', list);

  return (
    <GroupCheckList1Context.Provider value={list}>
      {list.map((checks2, index) => (
        <Check2 key={getUpdateVersionKey(checks2)} list={list[index]} index={index} />
      ))}
    </GroupCheckList1Context.Provider>
  );
}
export function GroupCheck({ group }: { group: IGroup<any> }) {
  const logger = new Log(false, 'GroupCheck_fn');
  logger.isPaused = true;
  const [isSequential] = useProxyWatch(group, 'isSequential', group.isSequential);
  const [l] = useProxyWatch(group, 'list', group.list);
  const [list] = useWatchUpdates(l);
  const [listSize] = useProxyWatch(list, 'length', list.length);
  const [c] = useProxyWatch(group, 'checks', group.checks);
  const [checks1] = useWatchUpdates(c);
  function updateChecks2(){
    logger.log('updateChecks2_fn', 'listSize=', listSize, group.checks);
    group.checks.forEach(checks2 => {
      logger.log('updateChecks2_fn', 'checks2=', checks2);
      for (let i = checks2.length; i < listSize; i++) {
        checks2.push(true);
      }
      checks2.length = listSize;
    });
  }
  useEffect(() => {
    updateChecks2();
  }, [listSize]);
  // logger.log('group=', group);
  // logger.log('checks1=', checks1);
  // logger.log('isSequential=', isSequential);
  logger.log('list.length=', list.length);
  if (list.length <= 1 || isSequential) return <></>;
  return (
    <Box>
      <Grid2 container>
        <Grid2>
          <Button
            data-id="group-add-check-list.875aec4f-5049-426a-a395-0ac0c6666a35"
            onClick={() => {
              group.checks.push(new Array(listSize).fill(true));
            }}>
            
          </Button>
        </Grid2>
      </Grid2>
      <Check1 list={group.checks} />
    </Box>
  );
}
