import { ITriggerJavaData, ITriggerShellData, TriggerContext } from "@droid/project-interface";
import { ITriggerData } from "@droid/project-interface";
import { Box, Button } from "@mui/material";
import { getTriggerUtils } from "@droid/project-interface";
import { getParent, toProxy, ButtonConfirm } from "@fanfanlo";
import { useContext } from "react";
import { TriggerListContext } from "@droid/project-interface";
export function Trigger({triggerData}:{triggerData:ITriggerData<ITriggerShellData, ITriggerJavaData>}){
    const list = useContext(TriggerListContext)
    const utils = getTriggerUtils(triggerData.type)
    const onDeleteComfirmed = () => {
        const index = list.indexOf(toProxy(triggerData))
        if(index === -1) return
        toProxy(list).splice(index, 1)
    }
    return (
        <TriggerContext.Provider value={triggerData}>
            <Box>
                {utils.UI(triggerData)}
                <Box sx={{textAlign:"center"}}>
                    <ButtonConfirm title="" content="" onConfirm={onDeleteComfirmed}>
                        <Button 
                        id="del-trigger-btn"
                        data-id="del-trigger-btn"
                        ></Button>
                    </ButtonConfirm>
                </Box>
            </Box>
        </TriggerContext.Provider>
    )
}

    
