import { Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField } from "@mui/material"
import { PropsWithChildren, useState } from "react"
import { useTranslation } from "react-i18next"
interface IEditableTextProps {
    setText: (text: string) => void
    title?: string
    cancelText?: string
    saveText?: string
    contentText?: string
    helperText?: string
    minHeight?: string
}
import React from 'react'
export function EditableText({ setText, title, cancelText, saveText, contentText, helperText, minHeight, children }: IEditableTextProps & PropsWithChildren) {
    const { t } = useTranslation('fanfanlo/mui/dialog/content')
    const [editText, setEditText] = useState('')
    const [isEditing, setIsEditing] = useState(false)
    function handleEdit(s: string) {
        setEditText(s)
    }
    return (
        <>
            <span style={{cursor: 'pointer', width: '100%', textAlign: 'center', display:"inline-block", backgroundColor: '', minHeight:minHeight || '1rem'}} onClick={() => setIsEditing(true)}><span >{children || ''}</span></span>
            {isEditing && (
                <Dialog open={isEditing} onClose={() => setIsEditing(false)}>
                    <DialogTitle>{title ||t('EditableText.title')}</DialogTitle>
                    {contentText && <DialogContent>{contentText}</DialogContent>}
                    <DialogContent>
                        <TextField
                        id="editable-text"
                        aria-label="editable-text"
                            autoFocus
                            required
                            fullWidth
                            helperText={helperText}
                            variant="standard"
                            value={editText} onChange={(e) => handleEdit(e.target.value)} />

                    </DialogContent>
                    <DialogActions>
                        <Button
                        id="editable-text-cancel"
                        data-id="editable-text-cancel"
                        onClick={() => setIsEditing(false)}>{cancelText || t('EditableText.cancel')}</Button>
                        <Button
                        id="editable-text-save"
                        data-id="editable-text-save"
                        onClick={() => { setIsEditing(false); setText(editText) }}>{saveText || t('EditableText.save')}</Button>
                    </DialogActions>
                </Dialog>
            )}
        </>
    )
}

