import { ITriggerAction, TriggerActionContext } from "@droid/project-interface";
import { EditableText, getParentWatchProxy } from "@fanfanlo";
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import HandymanIcon from '@mui/icons-material/Handyman';
import NotificationsNoneIcon from '@mui/icons-material/NotificationsNone';
import { Box, Grid2, IconButton } from "@mui/material";
import { useState } from "react";
import { ActionGroup } from "./action-group/ActionGroup";
import { TriggerGroup } from "./trigger-group/TriggerGroup";

import { ButtonConfirm } from "@fanfanlo";
export function TriggerAction({ triggerAction }: { triggerAction: ITriggerAction }) {
    const [name, setName] = useState(triggerAction.name || '')
    function handleNameChange(name: string) {
        triggerAction.name = name
        setName(name)
    }
    function handleDelete() {
        const parent = getParentWatchProxy<ITriggerAction[]>(triggerAction)
        if(!parent) return
        parent.splice(parent.indexOf(triggerAction), 1)
    }
    return (
        <TriggerActionContext.Provider value={triggerAction}>
            <Box sx={{ border: '2px solid #02450e', padding: '4px' }}
            data-id-trigger-action={triggerAction.id}
            >
                <Grid2 sx={{ alignItems: 'center' }} container>
                    <Grid2>
                        <NotificationsNoneIcon />
                        <HandymanIcon />
                    </Grid2>
                    <Grid2 size={"grow"}>
                        <EditableText setText={handleNameChange} >
                            <span
                            data-id="rename-trigger-action-btn.a4c6"
                            >{name}</span>
                        </EditableText>
                    </Grid2>
                    <Grid2>
                        <ButtonConfirm title="" content="" onConfirm={handleDelete}>
                            <IconButton
                            data-id="del-trigger-action-btn"
                            >
                                <DeleteOutlineIcon />
                            </IconButton>
                        </ButtonConfirm>
                    </Grid2>
                </Grid2>
                <TriggerGroup triggerGroup={triggerAction.triggerGroup} />
                <ActionGroup actionGroup={triggerAction.actionGroup} />
            </Box>
        </TriggerActionContext.Provider>
    )
}

