import { getUpdateVersionKey, useWatchUpdates } from "@fanfanlo"
import { useContext } from "react"
// import { ActionGroupContext } from "../../../../contexts"
import { Action } from "./action/Action"
import { ActionGroupContext } from "@droid/project-interface"
import _ from "lodash"
export function ActionList(){
    const actionGroup = useContext(ActionGroupContext)
    const [list] = useWatchUpdates(actionGroup.list)
    if(!list || !_.isArray(list)){
        console.log('lllllllllllllist is null', actionGroup)
    }
    return <div>
        {actionGroup.list.map(action=>{
            return <Action key={getUpdateVersionKey(action)} actionData={action} />
        })}
    </div>
}
