import { Dispatcher, IDispatcherEventMap, createDispatchEvent } from "@fanfanlo";

const hasWindow = typeof window !== "undefined";
export let androidJsGlobalName = "android"
export let android = hasWindow ? (window as any)[androidJsGlobalName] : '';
export const global = (hasWindow ? window.global || (window.global=window) : {}) as any;
export const jsBridgeDispatcher = new Dispatcher()

export interface ICheckBrowserCanBackEvent{
    canBack:boolean
}
export interface IJsBridgeEventMap extends IDispatcherEventMap{
    // js
    checkBrowserCanBack: (event:ICheckBrowserCanBackEvent)=>void
}
export function getWebViewJsId(){
    return android ? android.webViewJsId() : "-1";
}
const listeningMap:Record<string, any> = {}
// initJsBridge();
let initJsBridgeCalled = false
// js
export function initJsBridge(){
    if(!android)return;
    if(initJsBridgeCalled)return;
    initJsBridgeCalled = true
    global.onCallJsEvent = function (event:any){
        console.log("js bridge global event", JSON.stringify(event));
        if(!event)return;
        try {
            for (const [index, value] of Object.entries(listeningMap)) {
                if(value.callbackEventType !== event.type)continue;
                value.fun(event.data);
                if(value.once){
                    delete listeningMap[index];
                    // webViewJsListenoncejava
                    // webViewJsRemoveListen(value.target, index)
                }
            }
            const e = createDispatchEvent(event.data, undefined, undefined, event.type)
            jsBridgeDispatcher.dispatch(event.type, e)
            // json
            if(e.result !== undefined)return e.result 
            // __okkey__
            return "__okkey__"
        }catch (e){
            console.log('global on call js event error', e);
        }
        return ;
    }
}
export function webViewJsListen(target:any, type:any, callbackEventType:any, once:any, fun:any){
    if(!android)return -1;
    const index = android?.listen(target, type, callbackEventType, once)
    if(!index)return -1;
    let key = index.toString();
    try{
        key = `${typeof target === 'string' ? target : 'target'}-${typeof type === 'string' ? type : 'type'}-${typeof callbackEventType === 'string' ? callbackEventType : 'callbackEventType'}-${typeof index === 'string' ? index : index.toString()}`
    }catch(e){
        console.log('listen_fn 2 error', e)
    }
    listeningMap[key] = {once, fun, target, type, callbackEventType}
    return key;
}

export function webViewJsRemoveListen(target:any, index:any){
    if(!android)return;
    return android.removeListen(target, index)
}
